<template>
  <div class="create-wallet-by-mnemonic">
    <verification-modal
      ref="verification"
      :mnemonic-values="mnemonicValues"
      :mnemonic-done-modal-open="mnemonicDoneModalOpen"
      :spinner="spinner"
    />
    <div class="wrap">
      <div class="page-container">
        <div class="title">
          <h2>{{ $t('common.createANewWallet') }}</h2>
          <h5>{{ $t('createWallet.byMnemonicWriteDown') }}</h5>
        </div>
        <div class="contents">
          <div class="tools">
            <div class="value-switch noselect">
              <div class="sliding-switch">
                <label class="switch">
                  <input type="checkbox" />
                  <span
                    class="slider round"
                    @click="mnemonicValueBitSizeChange"
                  />
                </label>
                <div class="labels">
                  <span class="label-left white">12</span>
                  <span class="label-right">24</span>
                </div>
              </div>
            </div>

            <div
              v-touch:tap="mnemonicValueRefresh"
              class="random-button color-green noselect"
            >
              <i class="fa fa-refresh" aria-hidden="true" />
              <span>{{ $t('createWallet.byMnemonicRandom') }}</span>
            </div>
          </div>
          <div class="phrases">
            <ul>
              <li v-for="(value, index) in mnemonicValues" :key="index">
                {{ index + 1 }}.
                <span>{{ value }}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="button-container">
          <button
            v-touch:tap="mnemonicVerificationModalOpen"
            class="mid-round-button-green-filled"
          >
            {{ $t('createWallet.byMnemonicAlreadyWritten') }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import VerificationModal from './components/VerificationModal';
const bip39 = require('bip39');
import { Toast } from '@/helpers';
import { MnemonicWallet } from '@/wallets';

export default {
  components: {
    'verification-modal': VerificationModal
  },
  data() {
    return {
      verificationValues: [],
      mnemonicValues: [],
      mnemonic24: false,
      spinner: false
    };
  },
  computed: {
    ...mapGetters({
      web3: 'web3',
      wallet: 'wallet'
    })
  },
  mounted() {
    // Generate a random mnemonic
    this.mnemonicValues = bip39.generateMnemonic(128).split(' ');
  },
  beforeDestroy() {
    this.spinner = false;
  },
  methods: {
    mnemonicValueRefresh() {
      if (this.mnemonic24 === true) {
        this.mnemonicValues = bip39.generateMnemonic(256).split(' ');
      } else {
        this.mnemonicValues = bip39.generateMnemonic(128).split(' ');
      }
    },
    mnemonicValueBitSizeChange() {
      const left = document.querySelector('.label-left');
      const right = document.querySelector('.label-right');

      this.mnemonic24 = !this.mnemonic24;

      if (this.mnemonic24 === true) {
        // Regenerate new 24 Mnemonic phrases
        this.mnemonicValues = bip39.generateMnemonic(256).split(' ');
        left.classList.remove('white');
        right.classList.add('white');
      } else {
        // Regenerate new 12 Mnemonic phrases
        this.mnemonicValues = bip39.generateMnemonic(128).split(' ');
        left.classList.add('white');
        right.classList.remove('white');
      }
    },
    async mnemonicDoneModalOpen() {
      if (!this.isValidMnemonic()) return;
      try {
        this.spinner = true;
        const mnemonic = this.mnemonicValues.join(' ');
        const wallet = await MnemonicWallet(mnemonic, '');
        if (!this.web3.eth) this.$store.dispatch('setWeb3Instance');
        const currentWallet = await wallet.getAccount(0);
        currentWallet.mnemonic = mnemonic;
        await this.$store.dispatch('decryptWallet', [currentWallet]);
        this.$router.push({
          path: 'interface'
        });
      } catch (e) {
        Toast.responseHandler(e, Toast.ERROR);
      }
    },
    mnemonicVerificationModalOpen() {
      // Generate random numbers to choose which blocks to hide
      function generateNumArr(limit) {
        const ret = [];
        for (let i = 1; i < limit; i++) {
          ret.push(i);
        }

        return ret;
      }

      function shuffle(array) {
        let i = array.length;
        let j = 0;
        let temp;
        while (i--) {
          j = Math.floor(Math.random() * (i + 1));
          // swap randomly chosen element with current element
          temp = array[i];
          array[i] = array[j];
          array[j] = temp;
        }
        return array;
      }

      function inputHandler() {
        if (this.isValidMnemonic()) {
          this.$refs.verification.setValidMnemonic();
        } else {
          this.$refs.verification.setInvalidMnemonic();
        }
      }

      this.$refs.verification.setInvalidMnemonic();

      let ranNums = [];
      this.verificationValues = [];

      document.querySelectorAll('.phrases .word').forEach(function(el) {
        el.classList.remove('verification');
        el.querySelector('span').classList.remove('hidden');
        const inputEl = el.querySelector('input');
        inputEl.classList.add('hidden');
        inputEl.value = '';
        const newInputEl = inputEl.cloneNode(true);
        inputEl.parentNode.replaceChild(newInputEl, inputEl);
      });

      if (this.mnemonic24 === true) {
        ranNums = shuffle(generateNumArr(25));
      } else {
        ranNums = shuffle(generateNumArr(13));
      }

      // Hide 4 random mnemonic blocks
      for (let c = 0; c < 4; c++) {
        document
          .querySelector('.phrases .word[data-index="' + ranNums[c] + '"]')
          .classList.add('verification');

        document
          .querySelector('.phrases .word[data-index="' + ranNums[c] + '"]')
          .querySelector('span')
          .classList.add('hidden');
        this.verificationValues.push({
          word: document
            .querySelector('.phrases .word[data-index="' + ranNums[c] + '"]')
            .querySelector('span').textContent,
          no: ranNums[c]
        });

        const inputEl = document
          .querySelector('.phrases .word[data-index="' + ranNums[c] + '"]')
          .querySelector('input');

        inputEl.classList.remove('hidden');

        inputEl.addEventListener('input', inputHandler.bind(this));
      }

      this.$refs.verification.$refs.verification.show();
    },
    isValidMnemonic() {
      const validArr = [];

      this.verificationValues.forEach(function(value) {
        const userInputText = document
          .querySelector('.phrases .word[data-index="' + value.no + '"]')
          .querySelector('input')
          .value.toLowerCase();

        if (
          userInputText ===
          document
            .querySelector('.phrases .word[data-index="' + value.no + '"]')
            .querySelector('span').textContent
        ) {
          validArr.push(true);
        }
      });

      return validArr.length === this.verificationValues.length;
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'ByMnemonicContainer-desktop.scss';
@import 'ByMnemonicContainer-tablet.scss';
@import 'ByMnemonicContainer-mobile.scss';
</style>
